import React, { useEffect } from 'react'
import { useHistory } from 'react-router-dom'
import queryString from 'query-string'

import Grid from '@material-ui/core/Grid'

import GameTile from './GameTile'

import './GameLevel.css'

function GameLevel (props) {
    const history = useHistory()
    const { category } = queryString.parse(props.location.search)

    const handleGameTileClick = (value) => {
        //const category = props.location.state.category
        
        history.push({
            pathname: '/app/game',
            search: '?category=' + category + '&level=' + value,
            state: { category: category, level: value }
        })
    }

    useEffect(() => {
        localStorage.removeItem('wordsSolvedArray')
        localStorage.removeItem('currentWordTextFieldsSolved')
    });

    return (
        <div className='home'>
            <div className='game-body '>
                <Grid container direction='row' justify='center' alignItems='center' spacing={3}>
                    <Grid item xs={6} sm={3}>
                        <GameTile gameTile={{ value:'first-letter', title:'אוֹת פּוֹתַחַת', imageurl: process.env.PUBLIC_URL + '/first-letter.svg' }} onGameTileClick={handleGameTileClick}></GameTile>
                    </Grid>
                    <Grid item xs={6} sm={3}>
                        <GameTile gameTile={{ value:'last-letter', title:'אוֹת סוֹגֶרֶת', imageurl: process.env.PUBLIC_URL + '/last-letter.svg' }} onGameTileClick={handleGameTileClick}></GameTile>
                    </Grid>
                    <Grid item xs={6} sm={3}>
                        <GameTile gameTile={{ value:'middle-letter', title:'אוֹת אֶמְצָעִית', imageurl: process.env.PUBLIC_URL + '/middle-letter.svg' }} onGameTileClick={handleGameTileClick}></GameTile>
                    </Grid>
                    <Grid item xs={6} sm={3}>
                        <GameTile gameTile={{ value:'entire-word', title:'כָּל הַמִּלָּה', imageurl: process.env.PUBLIC_URL + '/entire-word.svg' }} onGameTileClick={handleGameTileClick}></GameTile>
                    </Grid>
                </Grid>
            </div>
        </div>
    )
}

export default GameLevel
