import React, { useState } from 'react'
import { useHistory } from 'react-router-dom'
import { Route, Redirect } from 'react-router-dom'

import Dashboard from './App/Dashboard'
import Footer from './App/Common/Footer'
import Menu from './App/Common/Header/Menu'
import MenuItem from './App/Common/Header/MenuItem'
import MenuButton from './App/Common/Header/MenuButton'

import './App.css';


function App() {

  const history = useHistory()

  const [isOpen, setIsOpen] = useState(false)
  const menu = ['מִשְׂחָק חָדָשׁ', 'אוֹדוֹת']
  const menuItems = menu.map((val, index) => (
      <MenuItem
          id={index}
          key={index}
          delay={`${index * 0.1}s`}
          onClick={handleLinkClick}>{val}</MenuItem>))
          
  function handleMenuClick(e) {
    e.preventDefault();
    // console.log('handleMenuClick.');
    setIsOpen(!isOpen )
  }
  function handleLinkClick(e) {
    e.preventDefault();
    // console.log('handleLinkClick')
    switch(e.target.id) {
      case '0': //new game
        history.push({ pathname: '/'})
        setIsOpen(!isOpen )
        break;
      case '1': //about
        history.push({ pathname: '/app/about'})
        setIsOpen(!isOpen )
        break;
      default:
        break;
    }
  }

  return (
    <div className='app'>
        <div className='content'>
          <div className='container'>
            <MenuButton open={isOpen} onClick={handleMenuClick} color='white'/>
            <div className='logo'>שלום כיתה א - משחק לימוד איות</div>
          </div>
          <Menu open={isOpen}>
              {menuItems}
          </Menu>
          <Route exact path='/' render={() => (
              <Redirect to='/app' />
          )} />
          <Dashboard />
        </div>
        <Footer />
    </div>
  );
}

export default App;
