import React from 'react'

import './Menu.css'

function Menu (props) {
    return (
        <div className={'menu-container ' + (props.open ? 'menu-height-100' : 'menu-height-0')}>
            {
                props.open
                    ? <div className='menu-list'>
                        {props.children}
                    </div> : null
            }
        </div>
    )
}

export default Menu