/* eslint-disable max-len */
import React from 'react'

import Card from '@material-ui/core/Card'
import CardActionArea from '@material-ui/core/CardActionArea'
import CardContent from '@material-ui/core/CardContent'
import CardMedia from '@material-ui/core/CardMedia'
import Typography from '@material-ui/core/Typography'

import './GameTile.css'

function GameTile (props) {
    return (
        <Card className='game-tile-root' variant='outlined' onClick={() => props.onGameTileClick(props.gameTile.value)}>
            <CardActionArea>
                <CardMedia className='game-tile-media' image={props.gameTile.imageurl} />
                <CardContent className='game-tile-card-content'>
                    <Typography gutterBottom variant='subtitle1'>
                        {props.gameTile.title}
                    </Typography>
                </CardContent>
            </CardActionArea>
        </Card>
    )
}

export default GameTile
